<template>
<div class="add-blog" id="add-blog">
    <h2 class="add-blog-content">添加博客</h2>
    <form v-if="!sumitted">
        <div class="container-detail">
            <label>博客标题</label>
            <input type="text" v-model="blog.title" required >
        </div>
        <div class="container-detail">
            <label>博客内容</label>
            <textarea v-model="blog.content" ></textarea>
        </div>
        <div class="checkbox">
            <label>vue.js</label>
            <input type="checkbox" value="vue.js" v-model="blog.category">
            <label>react.js</label>
            <input type="checkbox" value="react.js" v-model="blog.category">
            <label>agular.js</label>
            <input type="checkbox" value="angular.js" v-model="blog.category">
            <label>vue.js</label>
            <input type="checkbox" value="node.js" v-model="blog.category">
        </div>
        <div class="container-detail">
            <label>作者</label>
            <select v-model="blog.author">
                <option v-for=" author in authors" :key="author.id">
                    {{author}}
                </option>
            </select>
         </div>
        <button @click.prevent="post" class="add-btn">添加博客</button>
    </form>
    <div v-if="sumitted" class="add-success">您的博客发布成功!!!</div>
    <div class="preview">
        <h2>博客总览</h2>
        <h3>博客标题</h3>
        <p>{{blog.title}}</p>
        <h3>博客内容</h3>
        <p>{{blog.content}}</p>
        <h3>博客分类</h3>
        <ul>
            <li v-for="item in blog.category" :key="item.id">
                {{item}}
            </li>
        </ul>
        <h3>作者</h3>
        <p>{{blog.author}}</p>
    </div>
</div>
</template>

<script>
import AV from 'leancloud-storage'
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
          title:"",
          content:"",
          category:[],
          author:""
      },
      authors:["smith","david","daling"],
      sumitted:false
    }
  },
  methods:{   
      post:function(){
          debugger
          var _this=this
          var Todo = AV.Object.extend('Todo');
          var todo=new Todo();
              todo.set("title",_this.blog.title);
              todo.set("content",_this.blog.content);
              todo.set("lists",_this.blog.category);
              todo.set("author",_this.blog.author);
              todo.set("owner",_this.user);
          todo.save().then(function (todo){
             _this.sumitted=true;
          }).then(function(error){
              console.log("操作失败")
          })
      }
  },
  computed:{
      user:function(){
          return this.$store.state.userInfor
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@form-width:300px;
.bordered{border: 1px solid #ddd;border-radius: 5px;}
.margined {
    margin-bottom: 15px;
    margin-top: 15px;
}
.paddinged{padding:10px;}
.container-detail {
    label {
        display: block;
        .margined();
    }
    input,textarea,select{
        width: @form-width;
        .paddinged();
        .bordered()
    }
}
.checkbox {
    .margined();
}
@btn-width:100px;
.add-btn {
    width:btn-width;
    .paddinged();
    background-color: red;
    .bordered();
    color: #ffffff;
    .margined();
    cursor: pointer;
}
.preview {
    .bordered();
     padding: 0 10px;
}
.add-blog-content {
    text-align: center;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
}
.add-success {
    margin-bottom: 15px;
    color: red;
}
</style>



