<template>
    <header class="head">
        <div class="head-contain">
            <div class="head-left">
                <router-link :to="{name:'showblog'}" class="head-anchar">博客总览</router-link>
                <router-link :to="{name:'addblog'}" class="head-anchar">添加博客</router-link>
            </div>
             <div class="head-right" v-if="showYN">
                <router-link :to="{name:'loginblog'}" class="head-anchar">登录</router-link>
                <router-link :to="{name:'registryblog'}" class="head-anchar">注册</router-link>
            </div>   
            <div class="head-right" v-else>
                <span class="usercolor">{{username}}</span>
                <a @click="LoginOut">退出登录</a>
            </div> 
        </div>    
    </header>
</template>
<script>
import AV from 'leancloud-storage'
export default {
    data(){
        return {
           username:"",
           showYN:true
        }
    },
    created(){
            var currentUser = this.user;
            if(currentUser){
                this.showYN=false;
                this.username=currentUser.attributes.username;
            }
    },
    watch:{
        '$route' (to,from){
            var currentUser = this.user;
            if(currentUser){
                this.showYN=false;
                this.username=currentUser.attributes.username;
            }
            else{
                this.showYN=true;
            }
        }   
    },
    methods:{
        LoginOut:function(){
             var that=this;
             AV.User.logOut();
             this.$store.dispatch('increment',null)
             that.$router.push('/loginblog')   
        }
    },
    computed:{
        user:function(){
            return this.$store.state.userInfor
        }
    }
}
</script>
<style lang="less">
@head-width:980px;
.head {
    color: #ffffff;
    padding: 20px;
    background-color:rgba(0,0,0,0.3)
}
.head-contain {
    width: @head-width;
    margin: 0 auto;
    overflow: hidden;
}
.head-anchar {
    text-decoration: none;
    color: #ffffff;
    margin-right: 20px; 
}
.router-link-exact-active{
    color: #da9a20;
}
.head-left {
    float: left;
}
.head-right {
   float: right;
}
.usercolor {
    color: #da9a20;
    display: inline-block;
    margin-right: 20px;
}
</style>




