
<template>
    <div class="single-blog">
        <h3 class="single-h3">标题:{{blog.title}}</h3>
        <p>内容:{{blog.content}}</p>
        <ul>
               <li v-for="item in blog.lists" :key="item.id">
                   {{item}}
               </li>
           </ul>
           <p>{{blog.author}}</p>
        <div @click="deleteData" class="btn-sigle btn-sigle-red">删除</div>
        <router-link :to="'/editblog/'+id" class="btn-sigle">编辑</router-link>
    </div>  
</template>

<script>
import AV from 'leancloud-storage'
export default {
    name:"sigle-blog",
    data(){
        return {
            id:this.$route.params.id,
            blog:{}
        }
    },
    created:function(){
        var that=this;
       var query = new AV.Query('Todo');
       query.get(this.id).then(function (todo) {
            that.blog=todo.attributes;
        }, function (error) {
            console.log("操作错误")
        });
    },
    methods:{
        deleteData:function(){
            var that=this;
            var todo = AV.Object.createWithoutData('Todo', this.id);
            todo.destroy().then(function (success) {
                alert("删除成功")
                 that.$router.push({path:'/'})
            }, function (error) {
                alert("删除失败")
            })
      }
   }
}
</script>

<style>
    .single-blog {
        background-color:#eeeeee;
        padding: 20px;
        overflow: hidden;
    }
    .single-h3 {
        margin: 0;
    }
    .btn-sigle {
        float: left;
        padding: 10px 20px;
        background-color: #20b358;
        border-radius: 5px;
        margin-right: 15px;
        text-decoration: none;
        color: #ffffff;
    }
    .btn-sigle.btn-sigle-red {
        background-color:red;
    }
</style>




