<template>
    <div class="login-container">
       <form class="form-signup">
        <h2 class="form-signup-heading">注册新用户</h2>
         <div class="label-input">
        <label for="inputUsername" class="sr-only">用户名</label>
        <input type="text" class="form-control" placeholder="用户名" required autofocus v-model="userName">
         </div>
          <div class="label-input">
        <label for="inputPassword" class="sr-only">密码</label>
        <input type="password" class="form-control" placeholder="密码" required v-model="passWord">
          </div>
           <div class="label-input">
        <label for="inputEmail" class="sr-only">邮箱</label>
        <input type="email" class="form-control" placeholder="邮箱" required v-model="email">
           </div>
        <button class="btn btn-lg btn-primary btn-block" @click.prevent="InputRegistry">注册</button>
      </form>
    </div>
</template>
<script>
import AV from 'leancloud-storage'
export default {
  data(){
    return {
      userName:"",
      passWord:"",
      email:""
    }
  },
  methods:{
    InputRegistry:function(){
      var that=this;
      var user = new AV.User();
      user.setUsername(this.userName);
      user.setPassword(this.passWord);
      user.setEmail(this.email);
      user.signUp().then(function (loginedUser) {
        that.$store.dispatch('increment',loginedUser)
        that.$router.push("/showblog")
      }, (function (error) {
          alert(JSON.stringify(error));
      }));
    }
  }
}
</script>

<style scope lang="less">
.login-container {
    background-color: #ffffff;
    padding-bottom: 100px;
}
.form-signup {
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.label-input {
    margin-bottom: 15px;
}
.form-signup-heading{
text-align: center;
}
.form-signup .form-signup-heading,
.form-signup .checkbox {
  margin-bottom: 10px;
}
.form-signup .checkbox {
  font-weight: normal;
}
.form-signup .form-control {
  position: relative;
  height: auto;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid #dddddd;
}
.form-signup .form-control:focus {
  z-index: 2;
}
.sr-only {
    width: 60px;
    display: inline-block;
    text-align: right;
}
.btn-primary {
    width: 100%;
    background-color: #e0a328;
    padding: 10px 0;
    font-size: 16px;
    color: #ffffff;
    border: 0;
    border-radius: 10px;
}
</style>


