import Vue from 'vue'
import Router from 'vue-router'
import addBlog from './components/addBlog'
import showBlog from './components/showBlog'
import sigleBlog from './components/sigleBlog'
import editBlog from './components/editBlog'
import loginBlog from './components/loginBlog'
import registryBlog from './components/registryBlog'
import indexBlog from './components/indexBlog'
Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'indexblog',
      component: indexBlog,
    },
    {
      path: '/showblog',
      name: 'showblog',
      component: showBlog,
    },
    {
        path: '/addblog',
        name: 'addblog',
        component: addBlog
    },
    {
        path: '/sigleblog/:id',
        name: 'sigleblog',
        component: sigleBlog
    },
    {
      path: '/editblog/:id',
      name: 'editblog',
      component: editBlog
  },
  {
      path: '/loginblog',
      name: 'loginblog',
      component: loginBlog
  }, {
    path: '/registryblog',
    name: 'registryblog',
    component: registryBlog
  }
  ]
})


