import Vue from 'vue'
import Vuex from 'vuex'
import AV from 'leancloud-storage'
Vue.use(Vuex)
var APP_ID = 'Cxxu6T4hqVnohACY1rjJpGYf-gzGzoHsz';
var APP_KEY = 'redDh8mdRweGenuoTgcC5tRc';
AV.init({
    appId: APP_ID,
    appKey: APP_KEY
});
export const store = new Vuex.Store({
    state:{
        userInfor:AV.User.current()
        
    },
    mutations: {
        increment (state,payload) {
          // 变更状态
          state.userInfor=payload
        }
    },
    actions: {
        increment (context,payload) {
            context.commit('increment',payload)
        }
    }
})


