<template>
    <div class="login-container">
        <form class="form-signin">
        <h2 class="form-signin-heading">登录</h2>
        <div class="label-input">
            <label for="inputUsername" class="sr-only">用户名:</label>
            <input type="text" class="form-control" placeholder="用户名" required autofocus v-model="userName">
        </div>
        <div class="label-input">
            <label for="inputPassword" class="sr-only">密码:</label>
            <input type="password"  class="form-control" placeholder="密码" required v-model="passWord">
        </div>
        <button class="btn btn-lg btn-primary btn-block" @click.prevent="inputLogin">登录</button>
      </form>
    </div>
</template>
<script>
import AV from 'leancloud-storage'
export default {
    data(){
        return{
            userName:"",
            passWord:""
        }
    },
    methods:{
        inputLogin:function(){
            var that=this;
            AV.User.logIn(this.userName, this.passWord).then(function (loginedUser) {
                that.$store.dispatch('increment',loginedUser)
                that.$router.push('/showblog')      
            }, function (error) {
                alert("用户名或密码错误");
            });
        }
    }

}
</script>

<style scope>
.login-container {
    background-color: #ffffff;
    padding-bottom: 100px;
}
.form-signin {
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.label-input {
    margin-bottom: 15px;
}
.form-signin-heading {
    text-align: center;
}
.form-signin .checkbox {
  font-weight: normal;
}
.form-signin .form-control {
  position: relative;
  height: auto;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid #dddddd;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="username"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
}
.sr-only {
    width: 60px;
    display: inline-block;
    text-align: right;
}
.btn-primary {
    width: 100%;
    background-color: #e0a328;
    padding: 10px 0;
    font-size: 16px;
    color: #ffffff;
    border: 0;
    border-radius: 10px;
}
</style>



