<template>
    <div class="index-banner">
        <div class="index-banner-fil">
            <p>只能我们自己看的博客哦</p>
        </div>
    </div>
</template>
<script>
export default {
  name: 'App',
}
</script>

<style scoped>
.index-banner{
  background-image: url(/myblog/static/img/1.jpeg);
  position: absolute;
  width: 100%;
  height:100%;
  top: 0;
  left: 0;
  z-index: -2;
}
.index-banner-fil {
  width: 100%;
  height:100%;
  position: absolute;
  background-color: #000;
  filter:alpha(Opacity=60);
  -moz-opacity:0.6;
  opacity: 0.6;
  z-index:-1;
}
.index-banner-fil p{
  text-align: center;
  margin-top:25%;
  font-size: 30px;
  color: #ffffff;
}
</style>



