<template>
    <!--自定义指令中传的值是个字符串-->
    <div class="show-blog" v-theme:column="'wide'">
        <!--自定义指令的使用v-rainbow-->
        <h2 v-rainbow>博客总览</h2>
        <input type="search" placeholder="搜索" class="show-search" v-model="search">
        <router-link class="show-blog-inner" v-for="blog in filterBlogs" :key="blog.id" :to="'/sigleblog/'+blog.id">
            <!--管道标识-->
           <h3 v-rainbow class="show-title">{{blog.title | to-uppercase}}</h3>
           <article>{{blog.content | snippet}}</article> 
        </router-link>
    </div>
</template>
<script>
import AV from 'leancloud-storage'
export default{
    name:"show-blog",
    //局部组件过滤器注册
    data(){
        return{
            blogs:[],
            search:""
        }
    },
    created:function(){
        var that =this;
        var queList=[];
        var currentUser = that.user; 
        console.log(currentUser);
        var query = new AV.Query('Todo');      
        query.find().then(function (todos) {
            console.log(todos)
            todos.forEach(function(e){
              if(currentUser.id==e.attributes.owner.id){
                e.attributes['id']=e.id;
                that.blogs.push(e.attributes); 
               }
            })
        })
        console.log(that.blogs)
    },
    //搜索框的实现
    computed:{
        filterBlogs:function(){
            return this.blogs.filter((blog)=>{
                return blog.title.match(this.search)
            })
        },
        user:function(){
            return this.$store.state.userInfor
        }
    }
}
</script>
<style lang="less" scope>
.show-blog {
    padding: 20px;
    box-sizing: border-box;
}
.show-blog-inner {
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-top: 15px;
    margin-bottom:15px;
    display: block;
    cursor: pointer;
    text-decoration: none;
    padding:20px;
    background-color: #ffffff;
}
.show-search {
    width: 100%;
    height: 30px;
    border-radius: 5px;
    border: 1px solid #ddd;
    padding: 5px;
}
.show-title {
    font-size: 18px;
    font-weight:normal;
    margin: 0;
    margin-bottom: 10px;
}
</style>



